import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View, TextInput } from 'react-native';
import React, { useState, useEffect } from 'react';
import { DataTable } from 'react-native-paper';
import { usePagination } from 'react-table';
import { parse } from 'date-fns';


const query = `
  query ExampleQuery {
    launches {
      mission_name
      rocket {
        rocket_name
        rocket_type
      }
      launch_date_local
    }
  }
`;

function App() {
  const [launches, setLaunches] = useState([]);
  const [page, setPage] = useState(0);
  const [searchText, setSearchText] = useState('');
  const [filteredLaunches, setFilteredLaunches] = useState([]);

  async function getdata(){
    const response = await fetch('https://spacex-production.up.railway.app/', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ query }),
    })
    try {
      let data = await response.json();
      data.data.launches.forEach(function(launch) {
        launch.launch_date_local=launch.launch_date_local.substring(0,10).replace(/-/g,'/')
      })
      setLaunches(data.data.launches);
    } catch {
      console.error('data error');
    }
  }

  useEffect(() => {
    getdata()
  }, []);

  const itemsPerPage = 20;
  const fromIndex = page * itemsPerPage;
  const toIndex = (page + 1) * itemsPerPage;

  const pageCount = Math.ceil(launches.length / itemsPerPage);

  function handleSearch(text) {
    setSearchText(text);
    if (text === '') {
      setFilteredLaunches([]);
    } else {
      const filtered = launches.filter((launch) =>
        launch.mission_name.toLowerCase().includes(text.toLowerCase()) ||
        launch.rocket.rocket_name.toLowerCase().includes(text.toLowerCase()) ||
        launch.rocket.rocket_type.toLowerCase().includes(text.toLowerCase())
      );
      setFilteredLaunches(filtered);
    }
    setPage(0);
  }

  function handleDateSearch(text) {
    const parsedDate = parse(text, 'dd/MM/yyyy', new Date());
    const filtered = launches.filter((launch) =>
      launch.launch_date_local.startsWith(parsedDate.toISOString().substring(0, 10))
    );
    setFilteredLaunches(filtered);
    setPage(0);
  }
  const dataToDisplay = filteredLaunches.length > 0 || searchText ? filteredLaunches : launches;
  return (
    <View style={styles.container}>
      <TextInput
        style={styles.searchBar}
        placeholder="Search"
        value={searchText}
        onChangeText={handleSearch}
      />
      <TextInput
        style={styles.searchBar}
        placeholder="Search by date (dd/MM/yyyy)"
        onChangeText={handleDateSearch}
      />
      <DataTable style={styles.dataTable}>
        <DataTable.Header style={styles.header}>
          <DataTable.Title textStyle={styles.title}>Mission Name</DataTable.Title>
          <DataTable.Title textStyle={styles.title}>Rocket Name</DataTable.Title>
          <DataTable.Title textStyle={styles.title}>Rocket Type</DataTable.Title>
          <DataTable.Title textStyle={styles.title}>Launch Date</DataTable.Title>
        </DataTable.Header>
        {dataToDisplay.slice(fromIndex, toIndex).map((launch) => (
          <DataTable.Row key={launch.mission_name}>
            <DataTable.Cell>{launch.mission_name}</DataTable.Cell>
            <DataTable.Cell>{launch.rocket.rocket_name}</DataTable.Cell>
            <DataTable.Cell>{launch.rocket.rocket_type}</DataTable.Cell>
            <DataTable.Cell>{launch.launch_date_local}</DataTable.Cell>
          </DataTable.Row>
        ))}
        <DataTable.Pagination
          page={page}
          numberOfPages={pageCount}
          onPageChange={(newPage) => setPage(newPage)}
          label={`${fromIndex + 1}-${toIndex} of ${filteredLaunches.length > 0 ? filteredLaunches.length : launches.length}`}
        />
      </DataTable>
      <StatusBar style="auto" />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    alignItems: 'center',
    justifyContent: 'center',
  },
  searchBar: {
    width: '30%',
    height: 40,
    marginVertical: 10,
    paddingHorizontal: 10,
    borderColor: 'gray',
    borderWidth: 1,
    borderRadius: 5,
  },
  dataTable: {
    width: '50%',
  },
  header: {
    backgroundColor: 'pink',
  },
  title: {
    fontWeight: 'bold',
    fontFamily: 'Arial',
    fontSize: 20
  },
});

export default App